import React from "react";
import "../../../Style/Pages/Home/DashBoard.css";

const SelectDate = ({ startDate, endDate, onStartDateChange, onEndDateChange }) => {
  const handleStartDateChange = (e) => {
    onStartDateChange(e.target.value); // Memperbarui tanggal mulai
  };

  const handleEndDateChange = (e) => {
    onEndDateChange(e.target.value); // Memperbarui tanggal selesai
  };

  return (
    <div className="InputDateNavbarDashBoardBottom-Right">
      <div className="InputDateNavbarDashBoardBottom-Right">
        <label>Start Date:</label>
        <input className="date-filter-input" type="date" value={startDate} onChange={handleStartDateChange} placeholder="Filter by start date" />
      </div>
      <div className="InputDateNavbarDashBoardBottom-Right">
        <label>End Date:</label>
        <input className="date-filter-input" type="date" value={endDate} onChange={handleEndDateChange} placeholder="Filter by end date" />
      </div>
      <button className="DateButtonApply" onClick={() => console.log("Apply button clicked")}>
        Terapkan
      </button>
    </div>
  );
};

export default SelectDate;
